import React, { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';

import './index.scss';
import Avatar from '../Avatar';
import Icon, { IconTypes, IconColors } from '../Icon';
import IconButton from '../IconButton';
import ReactionButton from '../ReactionButton';
import ImageRenderer from '../ImageRenderer';
import MessageStatus from '../MessageStatus';
import Label, { LabelTypography, LabelColors } from '../Label';
import ContextMenu, { MenuItem, MenuItems, ReactionItems } from '../ContextMenu';
import EmojiReactions from '../EmojiReactions';
import {
  copyToClipboard,
  getMessageCreatedAt,
  getSenderName,
  getSenderProfileUrl,
} from './util';

const noop = () => { };
const SPACE_BETWEEN_MORE = 4;
const MORE_WIDTH = 32;

export default function Message(props) {
  const {
    isByMe,
    userId,
    message,
    className,
    resendMessage,
    showEdit,
    showRemove,
    status,
    useReaction,
    emojiContainer,
  } = props;

  if (!message) return null;

  const injectingClassName = (Array.isArray(className)) ? className : [className];
  injectingClassName.push(`sendbird-message${isByMe ? '--outgoing' : '--incoming'}`);

  const { emojiCategories = [] } = emojiContainer;
  const [emojiAllList, setEmojiAllList] = useState([]);
  const [emojiAllMap, setEmojiAllMap] = useState(new Map());

  useEffect(() => {
    if (useReaction) {
      const emojiList = [];
      const emojiMap = new Map();

      for (let i = 0; i < emojiCategories.length; i += 1) {
        const { emojis } = emojiCategories[i];
        for (let j = 0; j < emojis.length; j += 1) {
          emojiList.push({
            key: emojis[j].key,
            url: emojis[j].url,
          });
          emojiMap.set(emojis[j].key, emojis[j].url);
        }
      }
      setEmojiAllList(emojiList);
      setEmojiAllMap(emojiMap);
    }
  }, [emojiContainer]);

  return (
    <div className={[...injectingClassName, 'sendbird-message'].join(' ')}>
      {
        isByMe
          ? (
            <OutgoingUserMessage
              userId={userId}
              message={message}
              resendMessage={resendMessage}
              showEdit={showEdit}
              showRemove={showRemove}
              status={status}
              useReaction={useReaction}
              emojiAllMap={emojiAllMap}
              emojiAllList={emojiAllList}
            />
          )
          : (
            <IncomingUserMessage
              userId={userId}
              message={message}
              useReaction={useReaction}
              emojiAllMap={emojiAllMap}
              emojiAllList={emojiAllList}
            />
          )
      }
    </div>
  );
}

Message.propTypes = {
  isByMe: PropTypes.bool,
  userId: PropTypes.string.isRequired,
  message: PropTypes.objectOf(PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.bool,
    PropTypes.array,
    PropTypes.object,
  ])).isRequired,
  className: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.arrayOf(PropTypes.string),
  ]),
  showEdit: PropTypes.func,
  status: PropTypes.string,
  showRemove: PropTypes.func,
  resendMessage: PropTypes.func,
  useReaction: PropTypes.bool.isRequired,
  emojiContainer: PropTypes.shape({
    emojiCategories: PropTypes.arrayOf(PropTypes.shape({
      emojis: PropTypes.arrayOf(PropTypes.shape({})),
    })),
  }),
};

Message.defaultProps = {
  isByMe: false,
  resendMessage: noop,
  className: '',
  showEdit: noop,
  showRemove: noop,
  status: '',
  emojiContainer: {},
};

function OutgoingUserMessage({
  userId,
  message,
  showEdit,
  showRemove,
  status,
  resendMessage,
  useReaction,
  emojiAllMap,
  emojiAllList,
}) {
  // TODO: when message.requestState is succeeded, consider if it's SENT or DELIVERED
  const parentRefReactions = useRef(null);
  const parentRefMenus = useRef(null);
  const parentContainRef = useRef(null);

  return (
    <div
      className="sendbird-user-message--outgoing"
      style={{ paddingLeft: `${SPACE_BETWEEN_MORE + (useReaction ? MORE_WIDTH * 2 : MORE_WIDTH)}px` }}
    >
      <div className="sendbird-user-message__more" ref={parentContainRef}>
        <ContextMenu
          menuTrigger={(toggleDropdown) => (
            <IconButton
              className="sendbird-user-message__more__menu"
              ref={parentRefMenus}
              width="32px"
              height="32px"
              onClick={toggleDropdown}
            >
              <Icon width="24px" height="24px" type={IconTypes.MORE} fillColor={IconColors.PRIMARY} />
            </IconButton>
          )}
          menuItems={(closeDropdown) => (
            <MenuItems
              parentRef={parentRefMenus} // for catching location(x, y) of MenuItems
              parentContainRef={parentContainRef} // for toggling more options(menus & reactions)
              closeDropdown={closeDropdown}
              openLeft
            >
              <MenuItem
                onClick={() => { copyToClipboard(message.message); closeDropdown(); }}
              >
                Copy
              </MenuItem>
              <MenuItem
                onClick={() => { showEdit(true); closeDropdown(); }}
              >
                Edit
              </MenuItem>
              <MenuItem
                onClick={() => { showRemove(true); closeDropdown(); }}
              >
                Delete
              </MenuItem>
            </MenuItems>
          )}
        />
        {
          (useReaction && (emojiAllList.length > 0))
          && (
            <ContextMenu
              menuTrigger={(toggleDropdown) => (
                <IconButton
                  className="sendbird-user-message__more__add-reaction"
                  ref={parentRefReactions}
                  width="32px"
                  height="32px"
                  onClick={toggleDropdown}
                >
                  <Icon width="24px" height="24px" type={IconTypes.REACTIONS_ADD} fillColor={IconColors.PRIMARY} />
                </IconButton>
              )}
              menuItems={(closeDropdown) => (
                <ReactionItems
                  closeDropdown={closeDropdown}
                  parentRef={parentRefReactions}
                  parentContainRef={parentContainRef}
                >
                  {
                    emojiAllList.map((emoji) => (
                      <ReactionButton key={emoji.key} width="36px" height="36px" onClick={() => { closeDropdown(); }}>
                        <ImageRenderer
                          url={emoji.url}
                          width="28px"
                          height="28px"
                          defaultComponent={<Icon width="28px" height="28px" type={IconTypes.DUMMY} />}
                        />
                      </ReactionButton>
                    ))
                  }
                </ReactionItems>
              )}
            />
          )
        }
      </div>
      <div className="sendbird-user-message__text-balloon">
        <div className="sendbird-user-message__text-balloon__inner">
          <div className="sendbird-user-message__text-balloon__inner__text-place">
            <Label
              className="sendbird-user-message__text-balloon__inner__text-place__text"
              type={LabelTypography.BODY_1}
              color={LabelColors.ONBACKGROUND_1}
            >
              {message.message}
            </Label>
          </div>
          {
            message.reactions && message.reactions.length > 0
            && (
              <EmojiReactions
                className="sendbird-user-message__text-balloon__inner__emoji-reactions"
                userId={userId}
                messageReactions={message.reactions}
                emojiAllList={emojiAllList}
                emojiAllMap={emojiAllMap}
              />
            )
          }

        </div>
      </div>
      <div className="sendbird-user-message__status">
        <MessageStatus
          message={message}
          status={status}
          onDelete={() => { showRemove(true); }}
          onResend={() => resendMessage(message)}
        />
      </div>
    </div>
  );
}

function IncomingUserMessage({
  userId,
  message,
  useReaction,
  emojiAllList,
  emojiAllMap,
}) {
  const parentRefReactions = useRef(null);
  const parentRefMenus = useRef(null);
  const parentContainRef = useRef(null);
  const showReactionAddButton = useReaction && emojiAllList && (emojiAllList.length > 0);

  return (
    <div
      className="sendbird-user-message--incoming"
      style={{ paddingRight: `${SPACE_BETWEEN_MORE + (showReactionAddButton ? MORE_WIDTH * 2 : MORE_WIDTH)}px` }}
    >
      <Avatar
        className="sendbird-user-message__avatar"
        src={getSenderProfileUrl(message)}
        width="28px"
        height="28px"
      />
      <Label
        className="sendbird-user-message__sender-name"
        type={LabelTypography.CAPTION_2}
        color={LabelColors.ONBACKGROUND_2}
      >
        {getSenderName(message)}
      </Label>
      <div className="sendbird-user-message__text-balloon">
        <div className="sendbird-user-message__text-balloon__inner">
          <div className="sendbird-user-message__text-balloon__inner__text-place">
            <Label
              className="sendbird-user-message__text-balloon__inner__text-place__text"
              type={LabelTypography.BODY_1}
              color={LabelColors.ONBACKGROUND_1}
            >
              {message.message}
            </Label>
          </div>
          {
            message.reactions && message.reactions.length > 0
            && (
              <EmojiReactions
                className="sendbird-user-message__text-balloon__inner__emoji-reactions"
                userId={userId}
                messageReactions={message.reactions}
                emojiAllList={emojiAllList}
                emojiAllMap={emojiAllMap}
              />
            )
          }
        </div>
      </div>
      <Label
        className="sendbird-user-message__sent-at"
        type={LabelTypography.CAPTION_3}
        color={LabelColors.ONBACKGROUND_2}
      >
        {getMessageCreatedAt(message)}
      </Label>
      <div className="sendbird-user-message__more" ref={parentContainRef}>
        {
          showReactionAddButton
          && (
            <ContextMenu
              menuTrigger={(toggleDropdown) => (
                <IconButton
                  ref={parentRefReactions}
                  width="32px"
                  height="32px"
                  onClick={toggleDropdown}
                >
                  <Icon width="24px" height="24px" type={IconTypes.REACTIONS_ADD} fillColor={IconColors.PRIMARY} />
                </IconButton>
              )}
              menuItems={(closeDropdown) => (
                <ReactionItems
                  parentRef={parentRefReactions}
                  closeDropdown={closeDropdown}
                  parentContainRef={parentContainRef}
                >
                  {
                    emojiAllList.map((emoji) => (
                      <ReactionButton key={emoji.key} width="36px" height="36px" onClick={() => { closeDropdown(); }}>
                        <ImageRenderer
                          url={emoji.url}
                          width="28px"
                          height="28px"
                          defaultComponent={<Icon width="28px" height="28px" type={IconTypes.DUMMY} />}
                        />
                      </ReactionButton>
                    ))
                  }
                </ReactionItems>
              )}
            />
          )
        }
        <ContextMenu
          menuTrigger={(toggleDropdown) => (
            <IconButton
              ref={parentRefMenus}
              width="32px"
              height="32px"
              onClick={toggleDropdown}
            >
              <Icon width="24px" height="24px" type={IconTypes.MORE} fillColor={IconColors.PRIMARY} />
            </IconButton>
          )}
          menuItems={(closeDropdown) => (
            <MenuItems
              parentRef={parentRefMenus}
              parentContainRef={parentContainRef}
              closeDropdown={closeDropdown}
            >
              <MenuItem
                onClick={() => { copyToClipboard(message.message); closeDropdown(); }}
              >
                Copy
              </MenuItem>
            </MenuItems>
          )}
        />
      </div>
    </div>
  );
}

IncomingUserMessage.propTypes = {
  userId: PropTypes.string.isRequired,
  message: PropTypes.objectOf(PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.bool,
    PropTypes.array,
    PropTypes.object,
  ])),
  useReaction: PropTypes.bool.isRequired,
  emojiAllList: PropTypes.arrayOf(PropTypes.objectOf(PropTypes.string)).isRequired,
  emojiAllMap: PropTypes.instanceOf(Map).isRequired,
};

IncomingUserMessage.defaultProps = {
  message: {},
};

OutgoingUserMessage.propTypes = {
  userId: PropTypes.string.isRequired,
  message: PropTypes.objectOf(PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.bool,
    PropTypes.array,
    PropTypes.object,
  ])),
  showEdit: PropTypes.func,
  showRemove: PropTypes.func,
  resendMessage: PropTypes.func,
  status: PropTypes.string.isRequired,
  useReaction: PropTypes.bool.isRequired,
  emojiAllList: PropTypes.arrayOf(PropTypes.objectOf(PropTypes.string)).isRequired,
  emojiAllMap: PropTypes.instanceOf(Map).isRequired,
};

OutgoingUserMessage.defaultProps = {
  message: {},
  resendMessage: noop,
  showEdit: noop,
  showRemove: noop,
};
