import React, { useRef } from 'react';
import PropTypes from 'prop-types';

import './index.scss';

import ReactionBadge from '../ReactionBadge';
import ReactionButton from '../ReactionButton';
import ImageRenderer from '../ImageRenderer';
import Icon, { IconTypes } from '../Icon';
import ContextMenu, { ReactionItems } from '../ContextMenu';

export default function EmojiReactions({
  className,
  userId,
  messageReactions,
  emojiAllMap,
  emojiAllList,
}) {
  const injectingClassName = Array.isArray(className) ? className : [className];
  injectingClassName.unshift('sendbird-emoji-reactions');
  const imageWidth = '20px';
  const imageHeight = '20px';
  const emojiReactionAddRef = useRef(null);

  return (
    <div className={injectingClassName.join(' ')}>
      <div className="sendbird-emoji-reactions--inner">
        {
          messageReactions && (
            messageReactions.map((reaction) => {
              const { userIds = [] } = reaction;
              const reactedUserCount = userIds.length;
              const reactedByMe = userIds.indexOf(userId) > 0;
              const emojiUrl = emojiAllMap.get(reaction.key) || '';
              return (
                <ReactionBadge
                  key={reaction.key}
                  className="sendbird-emoji-reactions__emoji-reaction"
                  count={reactedUserCount}
                  selected={reactedByMe}
                >
                  <ImageRenderer
                    circle
                    url={emojiUrl}
                    width={imageWidth}
                    height={imageHeight}
                    defaultComponent={
                      <Icon width={imageWidth} height={imageHeight} type={IconTypes.EMOJI_FAILED} />
                    }
                  />
                </ReactionBadge>
              );
            })
          )
        }
        {
          (messageReactions.length < emojiAllList.length)
          && (
            <ContextMenu
              menuTrigger={(toggleDropdown) => (
                <ReactionBadge
                  isAdd
                  onClick={toggleDropdown}
                  className="sendbird-emoji-reactions__emoji-reaction-add"
                  ref={emojiReactionAddRef}
                >
                  <Icon
                    width={imageWidth}
                    height={imageHeight}
                    type={IconTypes.EMOJI_REACTIONS_ADD}
                  />
                </ReactionBadge>
              )}
              menuItems={(closeDropdown) => (
                <ReactionItems
                  parentRef={emojiReactionAddRef}
                  parentContainRef={emojiReactionAddRef}
                  closeDropdown={closeDropdown}
                >
                  {
                    emojiAllList.map((emoji) => (
                      <ReactionButton key={emoji.key} width="36px" height="36px" onClick={() => { closeDropdown(); }}>
                        <ImageRenderer
                          url={emoji.url}
                          width="28px"
                          height="28px"
                          defaultComponent={<Icon width="28px" height="28px" type={IconTypes.DUMMY} />}
                        />
                      </ReactionButton>
                    ))
                  }
                </ReactionItems>
              )}
            />
          )
        }
      </div>
    </div>
  );
}

EmojiReactions.propTypes = {
  userId: PropTypes.string.isRequired,
  className: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.arrayOf(PropTypes.string),
  ]),
  messageReactions: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  emojiAllList: PropTypes.arrayOf(PropTypes.objectOf(PropTypes.string)).isRequired,
  emojiAllMap: PropTypes.instanceOf(Map).isRequired,
};
EmojiReactions.defaultProps = {
  className: '',
};
